<template>
  <b-card no-body>
    <!-- <DialogHeader name="fontawesome"></DialogHeader> -->
    <b-card-header>
      <div class="mb-3" style="font-size:1.2em;text-bold;">
      21대 국회 의석수 계산기
      </div>

      <b-row class="p-1">
        <b-col class="col-1">
          지역구 예상 분석
        </b-col>
        <b-col class="col-11">
          <b-button
            class="col-poll mx-2 mt-1 mb-3"
