            variant="info"
            @click.prevent.stop="selectLocalSeatPoll(summarizedLocalSeat)">
            지역구 여론조사 종합
          </b-button>

          <b-button size="sm"
            class="col-poll mx-2 mt-1 mb-3"
            @click.prevent.stop="selectLocalSeatPoll(localSeatVote20)"
