            20대 의석수 대비
          </b-button>
          <b-button size="sm"
            class="col-poll mx-2 mt-1 mb-3"
            @click.prevent.stop="selectLocalSeatPoll(localSeatVote21Sample1)"
          >
            선거위키(2019-03-17)
            <b-badge pill variant="primary" class="col-label"
              @click.stop.prevent="goLink('http://blog.naver.com/PostView.nhn?blogId=electionwiki&logNo=221858590924&parentCategoryNo=&categoryNo=&viewDate=&isShowPopularPosts=false&from=postView')">연결
            </b-badge>
          </b-button>
          <b-button size="sm"
            class="col-poll mx-2 mt-1 mb-3"
            @click.prevent.stop="selectLocalSeatPoll(localSeatVote21Sample2)"
          >
            한국경제(2019-03-11)
            <b-badge pill variant="primary" class="col-label"
              @click.stop.prevent="goLink('https://www.hankyung.com/politics/article/202003112169i')">연결
            </b-badge>
          </b-button>
        </b-col>
      </b-row>
      <b-row class="p-1">
        <b-col class="col-1">
          비례의석 여론조사
        </b-col>
        <b-col class="col-11">
          <b-button
            class="col-poll mx-2 mt-1 mb-3"
            size="sm"
            variant="info"
            @click.prevent.stop="selectProportionalSeatPoll(summarizedProportionalSeatPoll)">
            비례대표 여론조사 종합<br />
            <small>3월 13일 ~ 현재</small>
          </b-button>
          <b-badge pill variant="primary" class="col-badge">{{ summarizedProportionalSeatPoll.respondent | digit }}명</b-badge>

          <template v-for="(poll, index) in proportionalSeatPollList">
          <b-button
            :key="index"
            class="col-poll mx-2 mt-1 mb-3"
            size="sm"
            @click.prevent.stop="selectProportionalSeatPoll(poll)">
            {{ poll.requester }}, {{ poll.research }}<br />
            <small>{{ poll.period }}</small>
          </b-button>
          <b-badge pill variant="primary" class="col-badge"> {{ poll.respondent | digit }}명</b-badge>
          </template>
        </b-col>
      </b-row>
      <b-row class="p-1">
        <b-col class="col-12 text-right">
          <b-button size="sm" class="mx-1" @click.stop.prevent="goLink('https://ko.wikipedia.org/wiki/%EB%8C%80%ED%95%9C%EB%AF%BC%EA%B5%AD_%EC%A0%9C21%EB%8C%80_%EA%B5%AD%ED%9A%8C%EC%9D%98%EC%9B%90_%EC%84%A0%EA%B1%B0_%EC%97%AC%EB%A1%A0_%EC%A1%B0%EC%82%AC')">비례대표 출처 : 위키피디아 한국 </b-button>
          <b-button size="sm" class="mx-1" @click.stop.prevent="goLink('http://www.ohmynews.com/NWS_Web/View/at_pg_w.aspx?CNTN_CD=A0002623791')">지도 정보 : 오마이뉴스</b-button>
          <b-button size="sm" class="mx-1" @click.stop.prevent="goLink('https://github.com/OhmyNews/2020_21_elec_map/blob/master/2020_21_elec_253_simple.json')">지도 정보 : GeoJson</b-button>
        </b-col>
      </b-row>
    </b-card-header>
    <b-card-body>
      <b-row>
        <b-col class="col-lg-9 col-12">
          <b-container fluid class="p-0">
          <b-row class="text-center py-1" style="font-size:1.1em;border-bottom:1px dashed;">
            <b-col :class="clazz(1)">정당</b-col>
            <b-col :class="clazz(2)">지역구 의석수</b-col>
            <b-col :class="clazz(2)">정당 지지율</b-col>
            <b-col :class="clazz(3)">환산 지지율</b-col>
            <b-col :class="clazz(3)">준연동형 비례</b-col>
            <b-col :class="clazz(3)">병립형 비례</b-col>
            <b-col :class="clazz(4, ['py-1', 'col-poll'])">합계
              <b-badge pill class="col-label">비율</b-badge>
            </b-col>
          </b-row>

          <template v-for="(key, index) in partyKey">
            <b-row :key="key" class="py-2 hover-row" style="border-bottom:1px dashed;">
              <b-col :class="clazz(1, ['py-1', 'col-poll'])">
                <template v-if="partyList[key].no">
                  {{ partyList[key].no }}.
                </template>
                {{ partyList[key].name }}
                <b-badge pill class="col-group" :style="{backgroundColor: partyGroup[partyList[key].group].color}">{{ partyGroup[partyList[key].group].name }}</b-badge>
              </b-col>

              <template v-if="!partyList[key].localLock">
                <b-col :class="clazz(2)">
                  <b-form-input
                    type="number"
                    v-model="input_party[index].localseat"
                    @keyup="calculate"
                    @change="calculate"
                    :disabled="partyList[key].localLock"
                    class="text-right"></b-form-input>
                </b-col>
              </template>
              <template v-else-if="!['part90', 'noparty', 'undecided'].includes(key)">
                <b-col :class="clazz(2, ['text-center', 'py-1'])">지역후보없음</b-col>
              </template>
              <template v-else>
                <b-col :class="clazz(2)"></b-col>
              </template>

              <template v-if="!partyList[key].ratioLock">
                <b-col :class="clazz(2)">
                  <b-form-input
                    type="number"
                    v-model="input_party[index].ratio"
                    @keyup="calculate"
                    @change="calculate"
                    :disabled="partyList[key].ratioLock"
                    class="text-right"></b-form-input>
                </b-col>
              </template>
              <template v-else-if="!['part90', 'noparty', 'undecided'].includes(key)">
                <b-col :class="clazz(2, ['text-center', 'py-1'])">비례후보없음</b-col>
              </template>
              <template v-else>
                <b-col :class="clazz(2)"></b-col>
              </template>

              <b-col :class="clazz(3, ['text-right', 'py-1', 'meaning'])">
                <template v-if="!['part90', 'noparty', 'undecided'].includes(key) && ratio100[key] > 0">
                  <strong :style="{color:partyList[key].color}">{{ ratio100[key] | ratio }}</strong>
                </template>
                <template v-else>
                  -
                </template>
              </b-col>

              <b-col :class="clazz(3, ['text-right', 'py-1', 'meaning'])">
                <template v-if="!['part90', 'noparty', 'undecided'].includes(key) && proportional_seat30[key] > 0">
                  <strong :style="{color:partyList[key].color}">{{ proportional_seat30[key] | digit }}</strong>
                </template>
                <template v-else>
                  -
                </template>
              </b-col>
              <b-col :class="clazz(3, ['text-right', 'py-1', 'meaning'])">
                <template v-if="!['part90', 'noparty', 'undecided'].includes(key) && proportional_seat17[key] > 0">
                  <strong :style="{color:partyList[key].color}">{{ proportional_seat17[key] | digit }}</strong>
                </template>
                <template v-else>
                  -
                </template>
              </b-col>
              <b-col :class="clazz(4, ['text-right', 'py-1', 'col-poll', 'meaning'])">
                <template v-if="!['part90', 'undecided'].includes(key) && seatRatio(index, key) > 0">
                  <strong :style="{color:partyList[key].color}">
                    <span class="mr-4">{{ (readLocalSeat(index) + proportional_seat30[key] + proportional_seat17[key]) | digit }}</span>
                  </strong>
                  <b-badge
                    pill
                    class="col-percent"
                    v-if="seatRatio(index, key) > 0"
                    :style="{backgroundColor: partyList[key].color}"
                  >
                    {{ seatRatio(index, key) | ratio }}
                  </b-badge>
                </template>
                <template v-else>
                  <span class="mr-4">-</span>
                </template>
              </b-col>
            </b-row>
          </template>
          <b-row class="mt-1">
            <b-col :class="clazz(1)">합계<small>(무당층 제외)</small></b-col>
            <b-col :class="clazz(2)">
                <b-form-input type="number"
                  v-model="input_localseat_total"
                  :disabled="true"
                  aria-describedby="validate-feedback-localseat-total"
                  class="text-right"></b-form-input>
                <!-- {{ getLocalSeatTotal | digit }} -->
            </b-col>
            <b-col :class="clazz(2)">
                <b-form-input type="number" v-model="input_ratio_total" :disabled="true" class="text-right"></b-form-input>
                <!-- {{ getRatioTotal | ratio }} -->
            </b-col>
            <b-col :class="clazz(3, ['text-right'])">
                <!-- <b-form-input type="number" v-model="input_ratio100_total" :disabled="true" class="text-right"></b-form-input> -->
                <!-- {{ input_ratio100_total | ratio }} -->
            </b-col>
            <b-col :class="clazz(3, ['text-right'])">
                <!-- <b-form-input type="number" v-model="input_proportional_seat30_total" :disabled="true" class="text-right"></b-form-input> -->
                {{ input_proportional_seat30_total | digit }}
            </b-col>
            <b-col :class="clazz(3, ['text-right'])">
                <!-- <b-form-input type="number" v-model="input_proportional_seat17_total" :disabled="true" class="text-right"></b-form-input> -->
                {{ input_proportional_seat17_total | digit }}
            </b-col>
            <b-col :class="clazz(4)">
                <b-form-input type="number" v-model="input_seat_total" :disabled="true" class="text-right"></b-form-input>
                <!-- {{ getSeatTotal | digit }} -->
            </b-col>
          </b-row>
          </b-container>
        </b-col>

        <b-col class="col-lg-3 col-12">
          <b-row class="mb-5">
            <b-col>
              <vote21-chart
                ref="party_group_chart"
                :chart-data="groupData"
                :options="chartOptions"
                height="400"
                >
              </vote21-chart>
            </b-col>
            <b-col>
              <vote21-chart
                ref="party_seat_chart"
                :chart-data="chartData"
                :options="chartOptions"
                height="400"
                >
              </vote21-chart>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      
      <div class="map-wrapper">
        <div v-if="province" class="province-title">
          <h2>{{province.SGG_2}} : {{ province.party }}, {{ province.candidate }}</h2>
          <div>
            <span v-html="province.metadata[1]"></span>
          </div>
        </div>
        <div v-if="currentProvince" class="province-info">
          <h3 class="text-center">{{currentProvince.SGG_3}}</h3>
          <ul>
            <li>정당: {{findPartyName(currentProvince.SGG_Code)}}</li>
            <li>SGG_Code: {{currentProvince.SGG_Code}}</li>
            <li>SGG_1: {{currentProvince.SGG_1}}</li>
            <li>SGG_2: {{currentProvince.SGG_2}}</li>
            <li>SGG_3: {{currentProvince.SGG_3}}</li>
          </ul>
        </div>
        <div class="province-summary">
          <h4 class="text-center">지역구 득표수</h4>
          <ul>
            <template v-for="(value, key) in summarizedLocalSeat">
              <li>{{ partyList[key].name }} : {{value | digit }}석</li>
            </template>
          </ul>
        </div>
      </div>

      <template v-for="(localSeatPoll, key) in localSeatPolls">
        <b-row>
          <b-col class="col-12 text-white">
            <strong>{{ localSeatCodes[key].SGG_2 }}</strong>
            <span
              class="mx-2 p-1"
              :style="{backgroundColor:findPartyColor(key)}"
              v-if="findPartyName(key)"
            >
              {{ findPartyName( key )}}
              , {{ findCandidate( key )}}
            </span>
          </b-col>
        </b-row>
        <b-row>
          <b-col class="col-12 pl-5">
            <template v-for="(poll) in localSeatPoll">
              <b-row>
                <b-col class="col-2">
                  {{ poll.requester }}, {{ poll.research }}<br />
                </b-col>
                <b-col class="col-10 text-right">
                  <b-progress class="mt-2" max="100" show-value>
                    <template v-for="item in poll.data">
                      <b-progress-bar :value="item.ratio" :precision="2" :style="{backgroundColor:partyList[item.party].color, color:'#ffffff'}">{{item.name}}({{item.ratio}}%)</b-progress-bar>
                    </template>
                  </b-progress>
                  <small>
                    응답자 : {{ poll.period }}, {{ poll.respondent | digit }}명
                    , <a href="#" @click.stop.prevent="goLink(poll.link)">출처</a>
                  </small>
                </b-col>
              </b-row>
              <b-row>
                <b-col class="col-12 pl-5">
                  <small>{{ poll.desc }}</small>
                </b-col>
              </b-row>
            </template>
          </b-col>
        </b-row>
      </template>

    </b-card-body>
    <b-card-footer>

    </b-card-footer>
  </b-card>
</template>



<script>
import Vue from 'vue'

const __TOTAL_SEAT = 300;
const __NO_RATIO_SEAT = 253;
const __RATIO_SEAT_CAP = 30;
const __RATIO_SEAT = __TOTAL_SEAT - __NO_RATIO_SEAT;
const __RATIO_SEAT_NO_CAP = __RATIO_SEAT - __RATIO_SEAT_CAP;

// const polls = require('./polls.json');
// console.log("polls : " + JSON.stringify(polls));

// https://ko.wikipedia.org/wiki/%EB%8C%80%ED%95%9C%EB%AF%BC%EA%B5%AD_%EC%A0%9C21%EB%8C%80_%EA%B5%AD%ED%9A%8C%EC%9D%98%EC%9B%90_%EC%84%A0%EA%B1%B0_%EC%97%AC%EB%A1%A0_%EC%A1%B0%EC%82%AC#5%EC%B0%A8_%EC%A0%95%EA%B3%84_%EA%B0%9C%ED%8E%B8_%EC%9D%B4%ED%9B%84_2
const proportionalSeatPolls = require('./proportional.poll.json');
const localSeatPolls = require('./localseat.poll').default;
const localSeatCodes = require('./localseat.code.json');

// import localSeatPolls from './localseat.poll'
// console.log(localSeatPolls);

// http://www.ohmynews.com/NWS_Web/View/at_pg_w.aspx?CNTN_CD=A0002623791
// https://github.com/OhmyNews/2020_21_elec_map/blob/master/2020_21_elec_253_simple.json
// https://namu.wiki/w/%EC%A0%9C21%EB%8C%80%20%EA%B5%AD%ED%9A%8C%EC%9D%98%EC%9B%90%20%EC%84%A0%EA%B1%B0/%EC%97%AC%EB%A1%A0%EC%A1%B0%EC%82%AC
// https://namu.wiki/w/%EC%A0%9C21%EB%8C%80%20%EA%B5%AD%ED%9A%8C%EC%9D%98%EC%9B%90%20%EC%84%A0%EA%B1%B0/%ED%9B%84%EB%B3%B4%EC%9E%90

// import { Vote21Chart } from './Vote21Chart'
const Vote21Chart = require('./Vote21Chart').default;

import * as d3 from 'd3'

export default {
  components: {
    // DialogHeader,
    "vote21-chart" : Vote21Chart
  },
  data() {
    return {
      partyGroup : {
        jinbo : {name : "진보", color:"#004EA2"},
        veryjinbo : {name : "급진보", color:"#FFCA05"},
        bosoo : {name : "보수", color:"#EF426F"},
        jungdo : {name : "중도", color:"#0BA95F"},
        etcs : {name : "기타", color:"#BBBBBB"},
      },
      partyKey : [
        "theminjoo", "unitedfuture", "minsaengdang", "futurekorea", "platformparty", "justice21", 
        "ourrepublican", "minjung", "koreaeconomy", "people21", "proparknew", "openminjoo", 
        "etcparty", "noparty", "undecided"
      ],
      partyList : {
        theminjoo : {name: "더불어민주당", no:1, localLock:false, ratioLock:true, group:"jinbo", color:"#004EA2"},
        unitedfuture : {name: "미래통합당", no:2, localLock:false, ratioLock:true, group:"bosoo", color:"#EF426F"},
        minsaengdang : {name: "민생당", no:3, localLock:false, ratioLock:false, group:"jungdo", color:"#0BA95F"},
        futurekorea : {name: "미래한국당", no:4, localLock:true, ratioLock:false, group:"bosoo", color:"#EF426F"},
        platformparty : {name: "더불어시민당", no:5, localLock:true, ratioLock:false, group:"jinbo", color:"#0088D2"},
        justice21 : {name: "정의당", no:6, localLock:false, ratioLock:false, group:"veryjinbo", color:"#FFCA05"},
        ourrepublican : {name: "우리공화당", no:7, localLock:false, ratioLock:false, group:"bosoo", color:"#009900"},
        minjung : {name: "민중당", no:8, localLock:false, ratioLock:false, group:"veryjinbo", color:"#F47920"},
        koreaeconomy : {name: "한국경제당", no:9, localLock:true, ratioLock:false, group:"bosoo", color:"#009900"},
        people21 : {name: "국민의당", no:10, localLock:true, ratioLock:false, group:"jungdo", color:"#EA5504"},
        proparknew : {name: "친박신당", no:11, localLock:false, ratioLock:false, group:"bosoo", color:"#BBBBBB"},
        openminjoo : {name: "열린민주당", no:12, localLock:true, ratioLock:false, group:"jinbo", color:"#004098"},
        etcparty : {name: "기타", localLock:false, ratioLock:false, group:"etcs", color:"#BBBBBB"},
        noparty : {name: "무소속", localLock:false, ratioLock:true, group:"etcs", color:"#BBBBBB"},
        undecided : {name: "무당층", localLock:true, ratioLock:false, group:"etcs", color:"#BBBBBB"},
      },
      input_party:[
        {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0},
        {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0},
        {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0},
        {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0},
        {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0},
        {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0},
        {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0},
        {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0},
        {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0},
        {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0}, {localseat : 0, ratio: 0},
      ],
      input_localseat_total: 0,
      input_ratio_total: 0,
      input_ratio100_total: 0,
      input_proportional_seat30_total: 0,
      input_proportional_seat17_total: 0,
      input_seat_total: 0,
      isLocalSeatInitialized: false,
      ratio100 : {},
      proportional_seat30 : {},
      proportional_seat17 : {},

      // pollList : polls,
      // selectedPoll : polls[0],
      localSeatCodes: localSeatCodes,
      localSeatPolls: localSeatPolls,
      summarizedLocalSeatPoll : {},
      summarizedLocalSeat: {},

      proportionalSeatPollList : proportionalSeatPolls,
      summarizedProportionalSeatPoll : {},
      selectedProportionalSeatPoll : {},



      localSeatVote20: {
        theminjoo:110, unitedfuture:105, people21:25, justice21:2, noparty:11
      },
      localSeatVote21Sample1: {
        theminjoo:135, unitedfuture:109, minsaengdang:2, noparty:7
      },
      localSeatVote21Sample2: {
        theminjoo:130, unitedfuture:119, justice21:1, minsaengdang:3, noparty:0
      },
      
      chartData : {
        labels: [],
        datasets: [{}]
      },
      
      groupData : {
        labels: [],
        datasets: [{}]
      },
      chartOptions : {
        // tooltips: {
        //     enabled: false
        // },
        tooltips: {
          callbacks: {
            label: function(tooltipItem, data) {
               let sum = 0;
              let dataArr = data.datasets[0].data;
              dataArr.map(data => {
                sum += data;
              });
              
              const value = data.datasets[0].data[tooltipItem.index];
              let percentage = value + ", " + (value * 100 / sum).toFixed(2) + "%";
              return data['labels'][tooltipItem['index']] + ': ' + percentage;
              // return data['labels'][tooltipItem['index']] + ': ' + data['datasets'][0]['data'][tooltipItem['index']] + '%';
            }
          }
        },
        legend: {
          labels: {
            fontColor: '#ffffff'
          }
        },
        // cutoutPercentage: 80,
        responsive: true,
        maintainAspectRatio: true,
        plugins: {
          datalabels: {
            formatter: (value, ctx) => {
              let sum = 0;
              let dataArr = ctx.chart.data.datasets[0].data;
              dataArr.map(data => {
                sum += data;
              });
              let percentage = value + (value * 100 / sum).toFixed(2) + "%";
              return percentage;
            },
            color: '#fff',
          }
        }
      }
      ,
      province: undefined,
      currentProvince: undefined,

    }
  },
  computed: {
  },
  created() {
    this.summaryProportionalSeatPoll();
    this.summaryLocalSeatPoll();
  },
  mounted() {
    this.selectProportionalSeatPoll(this.summarizedProportionalSeatPoll);
    this.selectLocalSeatPoll(this.summarizedLocalSeat);
    this.drawMap();
  },

  // http://watch.peoplepower21.org/election/
  methods: {
