    summaryLocalSeatPoll() {
      // {
      // 	"requester": "헤드라인제주, KCTV 제주방송, 제주일보, 제주투데이",
      // 	"research": "한국갤럽",
      // 	"period": "3월 28일 ~ 3월 29일",
      // 	"respondent": 505,
      // 	desc: "표본오차는 95% 신뢰수준에서 ±2.5%포인트(제주시 갑 ±4.4%포인트, 제주시을 ±4.4%포인트, 서귀포시 ±4.4%포인트)",
      // 	link: "http://www.headlinejeju.co.kr/news/articleView.html?idxno=413395",
      // 	data: [
      // 		{party: "theminjoo", name : "송재호", ratio: 34.8},
      // 		{party: "unitedfuture", name : "장성철", ratio: 28.0},
      // 		{party: "justice21", name : "고병수", ratio: 6.4},
      // 		{party: "ourrepublican", name : "문대탄", ratio: 0.4},
      // 		{party: "noparty", name : "박희수", ratio: 7.0},
      // 		{party: "noparty", name : "임효준", ratio: 0.0},
      // 		{party: "noparty", name : "현용식", ratio: 0.2},
      // 	]
      // }
    
      const summary = {};
      Object.keys(localSeatPolls).forEach(localcode => {
        const localSeatPoll = localSeatPolls[localcode];

        if(Array.isArray(localSeatPoll) && localSeatPoll.length > 0) {
          // console.log(localcode, localSeatPoll);

          localSeatPoll.forEach(poll => {
            
            const respondent = poll.respondent;   // 응답자
            const pollSummary = {};
            if(respondent > 0 && Array.isArray(poll.data) && poll.data.length > 0) {
              // console.log(localcode, localSeatPoll);

              poll.data.forEach(item => {
                const {party, name, ratio} = item;
                const key = party + "-" + name;

                // console.log(party, name, ratio);
                if(ratio >= 0) {
                  const value = ratio * respondent;
                  if(key in pollSummary) {
                    pollSummary[key].ratio += value;
                    pollSummary[key].respondent += respondent;
                  } else {
                    pollSummary[key] = {party, name, ratio : value, respondent};
                  }
                }
              });
            }

            // console.log(pollSummary);

            summary[localcode] = [];
            Object.keys(pollSummary).forEach(key => {
              const {party, name, ratio, respondent} = pollSummary[key];
              const newratio = ratio / respondent;

              summary[localcode].push({party, name, ratio : newratio})
            });

          });
        }

      });
      
      // console.log("summaryLocalSeatPoll Summary : ", summary);


      const summarizedLocalSeat = {};

      let total = 0;
      Object.keys(summary).forEach(code => {
        const poll = summary["" + code];
        if(poll && Array.isArray(poll) && poll.length > 0) {
          // console.log(poll);
          let party = "";
          let maxRatio = 0;
          
          poll.forEach(item => {
            if(maxRatio < item.ratio) {
              maxRatio = item.ratio;
              party = item.party;
              // console.log(key);
            }
          });
          
          if(party in summarizedLocalSeat) {
            summarizedLocalSeat[party]++;
          } else {
            summarizedLocalSeat[party] = 1;
          }
          total++;
        }
      });
      summarizedLocalSeat["undecided"] = __NO_RATIO_SEAT - total;
      console.log("summarizedLocalSeat : ", summarizedLocalSeat);



      this.summarizedLocalSeatPoll = summary;
      this.summarizedLocalSeat = summarizedLocalSeat;
      // this.summarizedLocalSeatPoll;
    },

    summaryProportionalSeatPoll() {
      // "requester": "SBS",
      // "research": "입소스",
      // "period": "3월 28일 ~ 3월 29일",
      // "respondent": 1001,
      // "minsaengdang": 0.60,
      // "futurekorea": 24.40,
      // "platformparty": 20.40,
      // "justice21": 13.50,
      // "ourrepublican": 0.80,
      // "minjung": 0.70,
      // "koreaeconomy": 0.40,
      // "people21": 5.30,
      // "proparknew": 1.00,
      // "openminjoo": 11.90,
      // "undecided": 20.90
      const sum = {};
      proportionalSeatPolls.forEach(item => {
          const respondent = item.respondent;   // 응답자
          Object.keys(item).forEach(key => {
            if(key !== "requester" && key !== "research" && key !== "period" && key !== "respondent") {
              const ratio = item[key];
              if(ratio >= 0) {
                const value = ratio * respondent;
                if(key in sum) {
                  sum[key].ratio += value;
                  sum[key].respondent += respondent;
                } else {
                  sum[key] = {ratio : value, respondent};
                }
              }
            }
          })
      });
      
      const summary = {};
      // console.log("proportionalSeatPolls Sum : ", sum);

      let respondent = 0;
      Object.keys(sum).forEach(key => {
        summary[key] = sum[key].ratio / sum[key].respondent;
        if(key === "undecided") {
          respondent = sum[key].respondent;
        }
      });

      // console.log("proportionalSeatPolls Summary : ", summary);

      this.summarizedProportionalSeatPoll = summary;
      this.summarizedProportionalSeatPoll.respondent = respondent;
    },

    validateState(name) {
      console.log(JSON.stringify(name));
      const { $dirty, $error } = this.$v.localseats[name];
      return $dirty ? !$error : null;
