        // this.selectedPoll.data[key] = this.selectedPoll.data[key] || {localseat:0, ratio: 0};
        const localseat = parseFloat(this.input_party[index].localseat) || 0;
        const ratio = parseFloat(this.input_party[index].ratio) || 0;

        totalLocalSeat += localseat;
        console.log("\t", this.partyList[key].name, localseat, ratio);
      });
      this.input_localseat_total = totalLocalSeat;



      let ratioSum = 0;
      // Object.keys(this.selectedPoll.data).forEach(key => {
