import Vue from 'vue'
import VueRouter from 'vue-router'
import Vote21 from '../views/Vote21.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Vote 21',
    component: Vote21
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
